import { Button, Form, Input } from 'antd'
import { Header } from "../../components/Header"
import CreateCSS from "./create.module.css"

export const CreateTeam = () => {

    const PlayerAdd = () => {
        return(
            <Form.Item
                label="Почта игрока"
                name="team_email">
                <Input />
            </Form.Item>
        )
    }

    return(
        <>
            <Header/>
            <div className="container">
                <div className={CreateCSS.content}>
                    <Form
                        name="Create-Team"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={() => {}}
                        onFinishFailed={() => {}}
                        autoComplete="on"
                        >
                        <Form.Item
                            label="Название команды"
                            name="team_name"
                            rules={[
                            {
                                required: true,
                                message: 'Пожалуйста, введите название команды!',
                            },
                            ]}
                        >
                            <Input />
                        </Form.Item>

                        <PlayerAdd/>
                        
                        <Button type="access" htmlType="submit">
                            Создать команду
                        </Button>
                    </Form>
                </div>
            </div>
        </>
    )
}